import { useTranslation } from "react-i18next";
import React, { useEffect, useState } from "react";
import Select from "react-select";
import { FaCode, FaColumns, FaFileAlt, FaPlus, FaTrash } from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Formik } from "formik";
import * as Yup from "yup";
import Autocomplete from "@material-ui/lab/Autocomplete";

import "./AddModal.css";

import bloomAPI from "api/common/bloom";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { TextField } from "@material-ui/core";
import i18n from "i18n/i18n";

export default function AddModal({
  modal,
  toggle,
  headerTitle,
  handleAddOnClick,
  handleCancelOnClick,
  subjectGoals,
}) {
  const { t } = useTranslation();

  const [isLoading, setIsLoading] = useState(false);
  const levels = [
    {
      value: 1,
      label: "I",
    },
    {
      value: 2,
      label: "T",
    },
    {
      value: 3,
      label: "U",
    },
  ];
  const [blooms, setBlooms] = useState([]);

  return (
    <Modal isOpen={modal} toggle={toggle}>
      <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
      <ModalBody>
        <div className="page">
          {isLoading && <LoadingBar />}

          <div className="page-content">
            <Formik
              initialValues={{
                ma: "",
                mota: "",
                mucTieuMonHoc: subjectGoals[0] ? subjectGoals[0].id : "",
                mucDo: [],
              }}
              enableReinitialize={true}
              validationSchema={Yup.object().shape({
                ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                mota: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
              })}
            >
              {({
                values,
                errors,
                dirty,
                isValid,
                handleChange,
                setFieldValue,
              }) => (
                <Form>
                  <FormGroup className="row">
                    <Label for="ma" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.CODE")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCode />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ma"
                          placeholder={t(
                            "admin.subject_output_standard_edit.CODE_PLACEHOLDER"
                          )}
                          value={values.ma}
                          onChange={handleChange}
                          invalid={errors.ma}
                        />
                      </InputGroup>
                      {errors.ma && (
                        <FormText color="danger">{errors.ma}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="mota" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.DESCRIPTION")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <Autocomplete
                          style={{ width: "100%" }}
                          inputValue={values.mota}
                          onInputChange={(event, newInputValue) => {
                            setFieldValue("mota", newInputValue);
                          }}
                          options={blooms}
                          renderInput={(params) => (
                            <TextField
                              {...params}
                              label={t(
                                "admin.subject_output_standard_edit.DESCRIPTION_PLACEHOLDER"
                              )}
                              margin="normal"
                              variant="outlined"
                            />
                          )}
                        />
                        {/*<Input
                          type="textarea"
                          name="mota"
                          placeholder={t(
                            "admin.subject_output_standard_edit.DESCRIPTION_PLACEHOLDER"
                          )}
                          value={values.mota}
                          onChange={handleChange}
                          invalid={errors.mota}
                          />*/}
                      </InputGroup>
                      {errors.mota && (
                        <FormText color="danger">{errors.mota}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="mucTieuMonHoc" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.SUBJECT_GOAL")}
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaColumns />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="mucTieuMonHoc"
                          value={values.mucTieuMonHoc}
                          onChange={handleChange}
                        >
                          {subjectGoals &&
                            subjectGoals.map((item, index) => {
                              return (
                                <option key={index} value={item.id}>
                                  {item.ma}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="mucDo" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.LEVEL")}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.mucDo}
                        onChange={async (e) => {
                          const levels = Array.isArray(e) ? e : [];
                          setFieldValue("mucDo", levels);
                          let newBlooms = await bloomAPI.getByLevels(
                            levels.map((item) => item.label)
                          );
                          newBlooms = newBlooms.map(item => item.dongTu);
                          setBlooms(newBlooms);
                        }}
                        isMulti
                        closeMenuOnSelect={false}
                        options={levels}
                        placeholder={t(
                          "admin.subject_output_standard_edit.LEVEL_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup>
                    <Button
                      color="success"
                      disabled={!(isValid && dirty)}
                      onClick={() => handleAddOnClick(values, isValid && dirty)}
                    >
                      <FaPlus /> {t("admin.common.ADD")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleCancelOnClick}>
                      <FaTrash /> {t("admin.common.CANCEL")}
                    </Button>
                  </FormGroup>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </ModalBody>
    </Modal>
  );
}
