import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import { FormGroup, Input, FormText, Button } from "reactstrap";

import "./Resource.css";

import syllabusAPI from "api/common/syllabus";

export default function Resource({
  syllabus,
  values,
  errors,
  setFieldValue,
  setIsLoading,
}) {
  const { t } = useTranslation();

  const [actionIsClose, setActionIsClose] = useState(true);

  const handleUpdateButtonOnClick = function () {
    const body = {
      taiNguyen: values.resource_resource,
    };
    setIsLoading(true);
    try {
      syllabusAPI.putById(syllabus.id, body);
      setActionIsClose(true);
      toast.success("UPDATE_SUCCESS");
    } catch (e) {
      toast.error(e.toString());
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancelButtonOnClick = function () {
    setActionIsClose(true);
  };

  return (
    <>
      <FormGroup className="row">
        {!actionIsClose && (
          <>
            <div className="col-lg-12 text-right">
              <Button color="success" onClick={handleUpdateButtonOnClick}>
                {t("admin.common.UPDATE")}
              </Button>{" "}
              &emsp;
              <Button onClick={handleCancelButtonOnClick}>
                {t("admin.common.CANCEL")}
              </Button>
            </div>
            <br />
            <br />
          </>
        )}
        <div className="col-lg-12">
          <Input
            style={{ minHeight: "400px" }}
            type="textarea"
            placeholder={t("admin.syllabus_edit.RESOURCE_PLACEHOLDER")}
            name="resource_resource"
            value={values.resource_resource}
            onChange={(e) => {
              setActionIsClose(false);
              setFieldValue("resource_resource", e.target.value);
            }}
            invalid={errors.resource_resource}
          />
          {errors.resource_resource && (
            <FormText color="danger">{errors.resource_resource}</FormText>
          )}
        </div>
      </FormGroup>{" "}
      <br />
    </>
  );
}
