import React from "react";
import { useTranslation } from "react-i18next";
import Select from "react-select";
import { FormGroup, Input, Label } from "reactstrap";

import "./Basic.css";

export default function Basic({ values }) {
  const { t } = useTranslation();

  return (
    <>
      <FormGroup className="row">
        <Label for="basic_subjectVietnameseName" className="col-lg-3">
          {t("admin.syllabus_edit.SUBJECT_VIETNAMESE_NAME")}
        </Label>
        <div className="col-lg-6">
          <Input
            type="text"
            name="basic_subjectVietnameseName"
            value={values.basic_subjectVietnameseName}
            disabled={true}
          />
        </div>
      </FormGroup>{" "}
      <br />
      <FormGroup className="row">
        <Label for="basic_subjectEnglishName" className="col-lg-3">
          {t("admin.syllabus_edit.SUBJECT_ENGLISH_NAME")}
        </Label>
        <div className="col-lg-6">
          <Input
            type="text"
            name="basic_subjectEnglishName"
            value={values.basic_subjectEnglishName}
            disabled={true}
          />
        </div>
      </FormGroup>{" "}
      <br />
      <FormGroup className="row">
        <Label for="basic_subjectCode" className="col-lg-3">
          {t("admin.syllabus_edit.SUBJECT_CODE")}
        </Label>
        <div className="col-lg-6">
          <Input
            type="text"
            name="basic_subjectCode"
            value={values.basic_subjectCode}
            disabled={true}
          />
        </div>
      </FormGroup>{" "}
      <br />
      <FormGroup className="row">
        <Label for="basic_knowledgeBlock" className="col-lg-3">
          {t("admin.syllabus_edit.KNOWLEDGE_BLOCK")}
        </Label>
        <div className="col-lg-6">
          <Input
            type="text"
            name="basic_knowledgeBlock"
            value={values.basic_knowledgeBlock}
            disabled={true}
          />
        </div>
      </FormGroup>{" "}
      <br />
      <FormGroup className="row">
        <Label for="basic_numberOfCredits" className="col-lg-3">
          {t("admin.syllabus_edit.NUMBER_OF_CREDITS")}
        </Label>
        <div className="col-lg-4">
          <Input
            type="number"
            name="basic_numberOfCredits"
            value={values.basic_numberOfCredits}
            disabled={true}
          />
        </div>
      </FormGroup>{" "}
      <br />
      <div className="ml-5">
        <FormGroup className="row">
          <Label for="basic_numberOfTheoreticalPeriods" className="col-lg-3">
            {t("admin.syllabus_edit.NUMBER_OF_THEORETICAL_PERIODS")}
          </Label>
          <div className="col-lg-4">
            <Input
              type="number"
              name="basic_numberOfTheoreticalPeriods"
              value={values.basic_numberOfTheoreticalPeriods}
              disabled={true}
            />
          </div>
        </FormGroup>{" "}
        <br />
        <FormGroup className="row">
          <Label for="basic_numberOfPracticePeriods" className="col-lg-3">
            {t("admin.syllabus_edit.NUMBER_OF_PRACTICE_PERIODS")}
          </Label>
          <div className="col-lg-4">
            <Input
              type="number"
              name="basic_numberOfPracticePeriods"
              value={values.basic_numberOfPracticePeriods}
              disabled={true}
            />
          </div>
        </FormGroup>{" "}
        <br />
        <FormGroup className="row">
          <Label for="basic_numberOfSelfStudyPeriods" className="col-lg-3">
            {t("admin.syllabus_edit.NUMBER_OF_SELF_STUDY_PERIODS")}
          </Label>
          <div className="col-lg-4">
            <Input
              type="number"
              name="basic_numberOfSelfStudyPeriods"
              value={values.basic_numberOfSelfStudyPeriods}
              disabled={true}
            />
          </div>
        </FormGroup>{" "}
        <br />
      </div>
      <FormGroup className="row">
        <Label for="basic_selectedSubjectPrerequisite" className="col-lg-3">
          {t("admin.syllabus_edit.SUBJECT_PREREQUISITE")}
        </Label>
        <div className="col-lg-6">
          <Select
            value={values.basic_selectedSubjectPrerequisite}
            isMulti
            closeMenuOnSelect={false}
            options={values.basic_subjectPrerequisites}
            placeholder={t(
              "admin.syllabus_edit.SUBJECT_PREREQUISITE_PLACEHOLDER"
            )}
            isDisabled={true}
          />
        </div>
      </FormGroup>{" "}
      <br />
    </>
  );
}
