import axiosClient from "./axiosClient";
import queryString from "query-string";

const tableName = "bloom";
export default {
  getAll: function (filter={}) {
    const query = queryString.stringify({page:0, ...filter });
    const url = `${tableName}?${query}`;
    return axiosClient.get(url);
  },
  getByLevels: function (levels) {
    if (levels.length <= 0) {
      return [];
    }
    let url = `${tableName}`;
    levels.map((level, index) => {
      if (index === 0) {
        url += `?mucDo=${level}`;
      } else {
        url += `&mucDo=${level}`;
      }
    });
    return axiosClient.get(url);
  },
};
