import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import { FaExchangeAlt, FaSave, FaSync, FaTrash } from "react-icons/fa";
import { Button, Form, FormGroup } from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./SyllabusUpdate.css";

import { RoutesString } from "pages/routesString";
import syllabusAPI from "api/common/syllabus";
import subjectPrerequisiteAPI from "api/common/subject-prerequisite";
import subjectGoalAPI from "api/common/subject-goal";
import subjectOutputStandardAPI from "api/common/subject-output-standard";
import evaluationTypeAPI from "api/common/evaluation-type";
import evaluationActivityAPI from "api/common/evaluation-activity";
import teachingActivityAPI from "api/common/teaching-activity";
import topicAPI from "api/common/topic";
import generalSyllbusAPI from "api/common/general-syllabus";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import Section from "./components/Section/Section";
import Basic from "./components/syllabus-content/Basic/Basic";
import Description from "./components/syllabus-content/Description/Description";
import Goal from "./components/syllabus-content/Goal/Goal";
import OutputStandard from "./components/syllabus-content/OutputStandard/OutputStandard";
import EvaluationType from "./components/syllabus-content/EvaluationType/EvaluationType";
import Evaluation from "./components/syllabus-content/Evaluation/Evaluation";
import TeachingActivity from "./components/syllabus-content/TeachingActivity/TeachingActivity";
import TheoreticalTeachingPlan from "./components/syllabus-content/TheoreticalTeachingPlan/TheoreticalTeachingPlan";
import PracticeTeachingPlan from "./components/syllabus-content/PracticeTeachingPlan/PracticeTeachingPlan";
import Resource from "./components/syllabus-content/Resource/Resource";
import Rule from "./components/syllabus-content/Rule/Rule";

export default function SyllabusEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const location = useLocation();

  const [id, setId] = useState(null);
  const [initialValues, setInitialValues] = useState({
    basic_subjectVietnameseName: "",
    basic_subjectEnglishName: "",
    basic_subjectCode: "",
    basic_numberOfCredits: "",
    basic_numberOfTheoreticalPeriods: "",
    basic_numberOfPracticePeriods: "",
    basic_numberOfSelfStudyPeriods: "",
    basic_subjectPrerequisites: [],
    basic_selectedSubjectPrerequisites: [],
    description_subjectDescription: "",
    goal_subjectGoals: [],
    outputStandard_subjectOutputStandards: [],
    evaluation_evaluationTypes: [],
    evaluation_evaluationActivities: [],
    teachingActivity_teachingActivities: [],
    theoreticalTeachingPlan_topics: [],
    practiceTeachingPlan_topics: [],
    resource_resource: "",
    rule_rule: "",
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [syllabus, setSyllabus] = useState(null);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      updateForm(parsed.id);
    }
  }, [location]);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      // -- 1. GET syllabus
      const syllabusRes = await syllabusAPI.getById(id);
      setSyllabus(syllabusRes);
      // -- 2. GET mon-hoc-tien-quyet
      const subjectPrerequisitesRes = await subjectPrerequisiteAPI.getBySubjectId(
        syllabusRes.monHoc.id
      );
      const subjectPrerequisitesOption = subjectPrerequisitesRes.contents.map(
        (item) => ({
          value: item.monHocTruoc.id,
          label: item.monHocTruoc.ma,
        })
      );
      // -- 3. GET muc-tieu-mon-hoc
      let pageable = {
        page: 1,
        limit: null,
        sorter: {
          sortBy: null,
          sortType: null,
        },
        searchKey: null,
        filter: {
          syllabus: syllabusRes.id,
        },
      };
      const subjectGoalRes = await subjectGoalAPI.getList(pageable);
      // -- 4. GET chuan-dau-ra-mon-hoc
      pageable = {
        page: 1,
        limit: null,
        sorter: {
          sortBy: null,
          sortType: null,
        },
        searchKey: null,
        filter: {
          subjectGoal: null,
          syllabus: syllabusRes.id,
        },
      };
      const subjectOutputStandardRes = await subjectOutputStandardAPI.getList(
        pageable
      );
      // -- 5. GET loai-danh-gia
      pageable = {
        page: 1,
        limit: null,
        sorter: {
          sortBy: null,
          sortType: null,
        },
        searchKey: null,
        filter: {
          idSyllabus: syllabusRes.id,
        },
      };
      const evaluationTypeRes = await evaluationTypeAPI.getList(pageable);
      // -- 6. GET hoat-dong-danh-gia
      pageable = {
        page: 1,
        limit: null,
        sorter: {
          sortBy: null,
          sortType: null,
        },
        searchKey: null,
        filter: {
          evaluationType: null,
          idSyllabus: syllabusRes.id,
        },
      };
      const evaluationActivityRes = await evaluationActivityAPI.getList(
        pageable
      );
      // -- 7. GET hoat-dong-day-hoc
      const teachingActivityRes = await teachingActivityAPI.getAll();
      // -- 8. GET chu-de (theory)
      const THEORY_ID = 2;
      pageable = {
        page: 1,
        limit: null,
        sorter: {
          sortBy: null,
          sortType: null,
        },
        searchKey: null,
        filter: {
          syllabus: syllabusRes.id,
          teachingPlanType: THEORY_ID,
        },
      };
      const theoreticalTeachingPlanRes = await topicAPI.getList(pageable);
      // -- 9. GET chu-de (practice)
      const PRACTICE_ID = 1;
      pageable = {
        page: 1,
        limit: null,
        sorter: {
          sortBy: null,
          sortType: null,
        },
        searchKey: null,
        filter: {
          syllabus: syllabusRes.id,
          teachingPlanType: PRACTICE_ID,
        },
      };
      const practiceTeachingPlanRes = await topicAPI.getList(pageable);
      // -- 10. UPDATE init values
      setInitialValues({
        basic_subjectVietnameseName: syllabusRes.monHoc.tenTiengViet,
        basic_subjectEnglishName: syllabusRes.monHoc.tenTiengAnh,
        basic_subjectCode: syllabusRes.monHoc.ma,
        basic_numberOfCredits: syllabusRes.monHoc.soTinChi,
        basic_numberOfTheoreticalPeriods: syllabusRes.monHoc.soTietLyThuyet,
        basic_numberOfPracticePeriods: syllabusRes.monHoc.soTietThucHanh,
        basic_numberOfSelfStudyPeriods: syllabusRes.monHoc.soTietTuHoc,
        basic_subjectPrerequisites: subjectPrerequisitesOption,
        basic_selectedSubjectPrerequisites: subjectPrerequisitesOption,
        description_subjectDescription: syllabusRes.moTa,
        goal_subjectGoals: subjectGoalRes.contents.map((item) => ({
          ...item,
          chuanDauRaCDIO: item.chuanDauRaCDIO.map((item) => ({
            value: item.id,
            label: item.ma,
          })),
        })),
        outputStandard_subjectOutputStandards: subjectOutputStandardRes.contents.map(
          (item) => ({
            ...item,
            mucDo: item.mucDo.split(/[ ,]+/).map((item) => ({
              value: item === "I" ? 1 : item === "T" ? 2 : 3,
              label: item,
            })),
          })
        ),
        evaluation_evaluationTypes: evaluationTypeRes.contents.map((item) => ({
          ...item,
          chuanDauRaMonHoc: item.chuanDauRaMonHoc.map((item) => ({
            value: item.id,
            label: item.ma,
          })),
        })),
        evaluation_evaluationActivities: evaluationActivityRes.contents.map(
          (item) => ({
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map((item) => ({
              value: item.id,
              label: item.ma,
            })),
          })
        ),
        teachingActivity_teachingActivities: teachingActivityRes.contents,
        theoreticalTeachingPlan_topics: theoreticalTeachingPlanRes.contents.map(
          (item) => ({
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map((item) => ({
              value: item.id,
              label: item.ma,
            })),
            hoatDongDanhGia: item.hoatDongDanhGia.map((item) => ({
              value: item.id,
              label: item.ma,
            })),
            hoatDongDayHoc: item.hoatDongDayHoc.map((item) => ({
              value: item.id,
              label: item.ten,
            })),
          })
        ),
        practiceTeachingPlan_topics: practiceTeachingPlanRes.contents.map(
          (item) => ({
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map((item) => ({
              value: item.id,
              label: item.ma,
            })),
            hoatDongDanhGia: item.hoatDongDanhGia.map((item) => ({
              value: item.id,
              label: item.ma,
            })),
            hoatDongDayHoc: item.hoatDongDayHoc.map((item) => ({
              value: item.id,
              label: item.ten,
            })),
          })
        ),
        resource_resource: syllabusRes.taiNguyen,
        rule_rule: syllabusRes.quiDinh,
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={t("admin.syllabus_edit.UPDATE_SYLLABUS")}
        items={[
          {
            href: RoutesString.ADMIN_SYLLABUS_LIST,
            title: t("admin.syllabus_list.SYLLABUS_LIST"),
          },
        ]}
      />

      <PageHeader titleHeader={t("admin.syllabus_edit.UPDATE_SYLLABUS")} />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({})}
        >
          {({
            values,
            errors,
            isValid,
            handleChange,
            handleReset,
            setFieldValue,
          }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />

              <Section
                title={t("admin.syllabus_edit.BASIC_TITLE")}
                content={<Basic values={values} />}
              />

              <Section
                title={t("admin.syllabus_edit.DESCRIPTION_TITLE")}
                content={
                  <Description
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.GOAL_TITLE")}
                content={
                  <Goal
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.OUTPUT_STANDARD_TITLE")}
                content={
                  <OutputStandard
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.EVALUATION_TYPE_TITLE")}
                content={
                  <EvaluationType
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.EVALUATION_TITLE")}
                content={
                  <Evaluation
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.TEACHING_ACTIVITY_TITLE")}
                content={
                  <TeachingActivity
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.THEORETICAL_TEACHING_PLAN_TITLE")}
                content={
                  <TheoreticalTeachingPlan
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.PRACTICE_TEACHING_PLAN_TITLE")}
                content={
                  <PracticeTeachingPlan
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.RESOURCE_TITLE")}
                content={
                  <Resource
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.RULE_TITLE")}
                content={
                  <Rule
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                    setIsLoading={setIsLoading}
                  />
                }
              />
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
